import React, { Component } from 'react';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Home from './HomeComponent';
import HelpCenter from './HelpCenter';
import {Route,Redirect, Router, Switch} from 'react-router-dom';
import { createBrowserHistory as createHistory } from "history";
const history = createHistory();
const images=
    [
    {name: `${process.env.PUBLIC_URL}/images/mcmaster0.jpg`, id:1,width:980,height:580},
    {name:`${process.env.PUBLIC_URL}/images/mcmaster1.jpg`, id:2,width:980,height:580},
    {name:`${process.env.PUBLIC_URL}/images/mcmaster2.jpg`, id:3,width:600,height:399},
    {name:`${process.env.PUBLIC_URL}/images/mcmaster3.jpg`, id:4,width:900,height:506},
    {name:`${process.env.PUBLIC_URL}/images/mcmaster4.jpg`, id:5,width:980,height:580},
    {name:`${process.env.PUBLIC_URL}/images/mcmaster5.jpg`, id:6,width:1170,height:610},
    ]

class Main extends Component {
  constructor(props){
    super(props);
    this.state={
      imageLib:images,
      imageSelected:{name: `${process.env.PUBLIC_URL}/images/mcmaster0.jpg`,id:1,width:980,height:580},
      curId:6
    }
  }
  render() {
    const HomePage=(props)=>{
      return(
        <Home {...props} 
        imageLib={this.state.imageLib} 
        imageSelected={this.state.imageSelected}
        curId={this.state.curId}
        changeImageSelected={(value)=>this.setState({imageSelected:value})} 
        changeImageLib={(value)=>this.setState({imageLib:value})}
        changeCurId={(value)=>this.setState({curId:value})} 
        />
      );
    }
    return (
      <div >
          <Header/>
            <Switch history ={history} >
              <Route path="/home"  exact component={HomePage} />
              <Route path="/helpcenter"component={HelpCenter} />
              <Redirect to="/home" ></Redirect>
            </Switch>
          <Footer />
      </div>
    );
  }
}

export default Main;
